@import '../../styles/texts.scss';

.Navigation--List--NavListItem {
  margin-top: 40.11px;

  a {
    @include default-link;
    font-size: 20px;
  }
}

@media(min-device-width: 1024px) {
  .Navigation--List--NavListItem {
    margin: 0;

    list-style-type: none;

    &:nth-child(1) {
      margin-right: 31px;
    }

    &:nth-child(3) {
      margin-right: 58px;
      margin-left: 31px;
    }

    &:nth-child(4) {
      margin-right: 58px;
    }

    a {
      color: #2E3D4B;

      &:after {
      display: block;
      content: '';
      border-bottom: 4px solid #B04527;
      transform: scaleX(0);
      transition: transform 250ms ease-in-out;
    }

      &:hover:after {
        transform: scaleX(1);
      }
    }
  }
}